<template>
  <div id="app">
    <div class='loading' v-if='loading'>
<half-circle-spinner
  :animation-duration="1000"
  :size="60"
   :color="'#080808'"
  v-if='loading' 
  class='main-spinner' 
/>
    </div>
    
    <div class="columns ">
  <!-- All other tile elemnts -->
    <div class="column is-12">
    <header class='header'>
      <Menu :fetchNews='getNews'></Menu>
      <Search :fetchNews='getNews'></Search>
    </header>
    </div>
    
    <div class="column is-11 is-offset-right-1 ">
    <main>
      <Lastest></Lastest>
      <Section></Section>

    </main>
    
    </div>
    </div>
    
  </div>
</template>

<script>
import {
    store
  }  from '@/store.js'

import Section from "@/components/Section.vue";
import Menu from "@/components/Menu.vue";
import Lastest from "@/components/Lastest.vue";
import Search from "@/components/Search.vue";
import {HalfCircleSpinner} from 'epic-spinners'
export default {
  name: "App",
  components: {
    Section,
    Menu,
    Lastest,
    Search,
    HalfCircleSpinner
  },
  store,
  methods: {
    getNews() {
      store.commit('loadingBind')
            fetch(`https://newsapi.org/v2/top-headlines?country=${this.$store.state.country}&pagesize=${this.$store.state.perPage}&category=${this.$store.state.category}&q=${this.$store.state.q}&apiKey=c70c6b881578467e8d65672d0783c383`)
              .then(resp => {

                return resp.json()
              })
              .then(resp => {
                console.log(resp)
                store.commit('addNewsData', resp.articles)
               store.commit('loadingBind')
              })
          }
  },
  data () {
    return {
      loading: true
    }
  },
  mounted() {
    this.getNews()
    this.loading = false
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto';
  color: #080808;
}

html {
  font-size: 62.5%;
}

.header {
  width: 100%;
  height: 52px;
  background: #fff;
  display: flex;
  justify-content: center;
 
  position: fixed;
  top:0;
  left: 0;
  z-index: 10;
  -webkit-backface-visibility: hidden;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}

.loading {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: 20;
  background: #fff;
  transition: .1s all ease-in-out;

}

.main-spinner {
  margin: 3rem;
}
</style>



