<template>
  <section class='section slider'>    
    <carousel  :perPage='1' paginationColor = "#f2f2f2" paginationActiveColor="#07020D" :autoplay='true'>
  <slide v-for='(item, index) in newsData' :key='index'>
      <div class='slider-item'>
          <div class="gradient"></div>
          <img :src='imageSrc[index]'>
          <p class='slider-item__content'>{{item.title}}</p>
      </div>
  </slide>
  
</carousel>
  </section>
</template>

<script>
import News from "@/components/News.vue";
import {
    store
  }  from '@/store.js'

export default {
  name: "Lastest",
  components: {
    News
  },
  computed: {
      newsData(){
          console.log(store.state.newsData)
          if(store.state.newsData.length > 0 ) {
              
            return store.getters.getFiveNewestNews
          }
          
      },
    imageSrc() {
        return this.newsData.map(function(item) {
          let imageUrl = item.urlToImage;
          return imageUrl === null ?  'https://images.vexels.com/media/users/3/144609/raw/26930bcc86d4e5962ffe712cae931fc8-world-breaking-news-banner-header.jpg': imageUrl  
        });
      }
  }
};
</script>

<style>
.slider {
    width: 100%;
    margin-top: 6rem;
}

.slider-item {
    position: relative;
    height: 212px;
}

.slider-item__content {
    position: absolute;
    bottom: 12px;
    width: 100%;
    padding: 1rem 3rem;
    font-size: 1.4rem;
    font-weight: 700;
    color: #fff;
}

.slider-item img {
    width: 100%;
    height: 100%;
}

.gradient {
    width: 100%;
    height: 100%;
    background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(131,131,131,0) 61%, rgba(55,55,55,1) 98%, rgba(51,51,51,1) 100%);
background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(131,131,131,0) 61%,rgba(55,55,55,1) 98%,rgba(51,51,51,1) 100%);
background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(131,131,131,0) 61%,rgba(55,55,55,1) 98%,rgba(51,51,51,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#333333',GradientType=0 );
    position: absolute
}
</style>



